<template>
  <div>
    <div class="flex items-center">
      <div class="flex items-center v-mr-auto">
        <a-space>
          <a-button type="primary">默认排序</a-button>
          <a-button>名称</a-button>
          <a-button>任务用时</a-button>
          <a-button>创建时间</a-button>
          <a-input-search placeholder="资源名称/任务名称" style="width: 200px" @search="onSearch" />
        </a-space>
      </div>
      <a-space>
        <a-icon type="appstore" class="text-primary" style="font-size: 18px" />
        <a-icon type="bars" style="font-size: 18px" />
      </a-space>
    </div>
    <a-divider></a-divider>
    <div class="task-container">
      <a-list :grid="{ gutter: 16, column: 4 }">
        <a-list-item>
          <a-card hoverable style="height: 169px" class="bg-grey-1">
            <router-link target="_blank" class="absolute text-center text-primary" style="left: 50%; top: 50%; transform: translate(-50%, -50%)" :to="{ path: '/training/task/build' }">
              <a-icon key="edit" type="plus" style="font-size: 36px" />
              <h4 class="v-mt-sm">创建任务</h4>
            </router-link>
          </a-card>
        </a-list-item>
        <a-list-item v-for="item in list" :key="item.id">
          <a-card hoverable>
            <!-- <img slot="cover" alt="example" src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png" /> -->
            <template slot="actions" class="ant-card-actions">
              <span class="ant-card-actions-item" @click="handleDel(item)"><i class="fa fa-trash-o" title="删除任务"></i></span>
              <span class="ant-card-actions-item" @click="handleSwitch(item)"
                ><i class="fa" :class="[item.closed ? 'fa-eye' : 'fa-eye-slash']" :title="item.closed ? '开启任务' : '关闭任务'"></i
              ></span>
              <router-link class="ant-card-actions-item" target="_blank" :to="{ path: '/training/task/edit' }"><a-icon key="edit" type="edit" title="编辑任务" /></router-link>
              <span class="ant-card-actions-item" @click="handleAssign(item)"><i class="fa fa-paper-plane" title="分配选手"></i></span>
            </template>
            <a-card-meta :title="item.name">
              <div slot="description" class="font-xs">
                <div class="flex items-center v-mb-xs">
                  <span>选手人次：</span>
                  <b>{{ item.people }}</b>
                </div>
                <div class="flex items-center v-mb-xs">
                  <span>评卷任务：</span>
                  <b>{{ item.task }}个</b>
                </div>
                <div class="flex items-center v-mb-xs">
                  <span>用时时长：</span>
                  <b>{{ item.period }}小时</b>
                </div>
                <div class="flex items-center v-mb-xs">
                  <span>上线时间：</span>
                  <b>{{ item.publishTIme }}</b>
                </div>
              </div>
              <!-- <a-avatar slot="avatar" src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" /> -->
            </a-card-meta>
          </a-card>
        </a-list-item>
      </a-list>
    </div>
    <a-modal :confirmLoading="assignSubmiting" centered title="分配选手" :width="690" v-model="showAssignModal" @ok="handleAssignSubmit" okText="确认" cancelText="取消">
      <div class="v-mb-md font-md">目标任务：{{ record.name }}</div>
      <a-transfer
        :data-source="mockData"
        :titles="['未分配选手', '已分配选手']"
        :target-keys="targetKeys"
        :selected-keys="selectedKeys"
        :render="(item) => item.title"
        :list-style="{
          width: '300px',
          height: '350px',
        }"
        @change="handleAssignChange"
        @selectChange="handleAssignSelectChange"
      />
    </a-modal>
  </div>
</template>

<script>
export default {
  data() {
    let list = []
    for (let index = 1; index < 7; index++) {
      list.push({
        id: index,
        name: '热敏机打印程序设计' + index,
        publishTIme: '2021-04-21 16:' + (50 - index),
        people: 12 + index,
        task: index,
        period: 2.5,
        closed: false,
      })
    }
    const mockData = []
    for (let i = 0; i < 20; i++) {
      mockData.push({
        key: i.toString(),
        title: `选手${i + 1}`,
        description: `选手描述${i + 1}`,
      })
    }
    const oriTargetKeys = mockData.filter((item) => +item.key % 3 > 1).map((item) => item.key)
    return {
      mockData,
      targetKeys: oriTargetKeys,
      selectedKeys: ['1', '4'],
      list,
      showAssignModal: false,
      assignSubmiting: false,
      record: {},
    }
  },
  methods: {
    handleSwitch(item) {
      this.$confirm({
        title: `确认${item.closed ? '开启' : '关闭'}任务【${item.name}】？`,
        // content: 'Some descriptions',
        okText: '确认',
        // okType: 'danger',
        cancelText: '取消',
        onOk() {
          setTimeout(() => {
            item.closed = !item.closed
          }, 500)
        },
        onCancel() {
          console.log('Cancel')
        },
      })
    },
    handleDel(item) {
      this.$confirm({
        title: `确认删除任务【${item.name}】？`,
        // content: 'Some descriptions',
        okText: '确认删除',
        okType: 'danger',
        cancelText: '取消',
        onOk() {
          console.log('OK')
        },
        onCancel() {
          console.log('Cancel')
        },
      })
    },
    handleAssignChange(nextTargetKeys, direction, moveKeys) {
      this.targetKeys = nextTargetKeys
      console.log('targetKeys: ', nextTargetKeys)
      console.log('direction: ', direction)
      console.log('moveKeys: ', moveKeys)
    },
    handleAssignSelectChange(sourceSelectedKeys, targetSelectedKeys) {
      this.selectedKeys = [...sourceSelectedKeys, ...targetSelectedKeys]

      console.log('sourceSelectedKeys: ', sourceSelectedKeys)
      console.log('targetSelectedKeys: ', targetSelectedKeys)
    },
    handleAssign(item) {
      this.record = item
      this.showAssignModal = true
    },
    handleAssignSubmit() {
      this.assignSubmiting = true
      setTimeout(() => {
        this.assignSubmiting = false
        this.showAssignModal = false
      }, 500)
    },
  },
}
</script>

<style lang="stylus"></style>
